<template>
  <div class="container container-sm">
    <div class="block">
      <div class="block-header">會員資料與收費 </div>
      <div class="block-body">
        <accordion>
          <template slot="title"> {{profile.email}} </template>
          <template slot="btn">更改基本資料 </template>
          <div slot="body">
            <div class="form">
              <div class="form-group">
                <label for="">暱稱</label> <input v-model="profile.nickname"
                  type="text"
                  placeholder="暱稱"
                  name="nickname"
                  v-validate="'required'"
                  class="form-control"
                  :class="{'is-invalid':errors.has('nickname')}">
                <div v-show="errors.has('nickname')"
                  class="invalid-feedback">{{ errors.first('nickname') }}</div>
              </div>
              <div class="form-group">
                <label for="">電話</label> <input v-model="profile.phone"
                  type="text"
                  placeholder="phone"
                  class="form-control"
                  v-validate="'required'"
                  name="phone"
                  :class="{'is-invalid':errors.has('phone')}">
                <div v-show="errors.has('phone')"
                  class="invalid-feedback">{{ errors.first('phone') }}</div>
              </div>
              <div class="form-group">
                <button class="btn btn-primary"
                  @click="updateBasic">修改</button>
              </div>
            </div>
          </div>
        </accordion>
        <accordion>
          <template slot="title"> ****** </template>
          <template slot="btn"> 更改密碼 </template>
          <div slot="body">
            <div class="form">
              <div class="form-group">
                <label for="">舊密碼</label> <input v-model="profile.oldPassword"
                  type="password"
                  placeholder="舊密碼"
                  class="form-control"
                  v-validate="'required'"
                  name="oldPassword"
                  :class="{'is-invalid':errors.has('oldPassword')}">
                <div v-show="errors.has('oldPassword')"
                  class="invalid-feedback">{{ errors.first('oldPassword') }}</div>
              </div>
              <div class="form-group">
                <label for="">新密碼 (新密碼（4 至 60 個字元）)</label> <input v-model="profile.newPassword"
                  type="password"
                  placeholder="新密碼"
                  class="form-control"
                  v-validate="'required|min:4|max:60|confirmed:confirmPassword'"
                  name="newPassword"
                  :class="{'is-invalid':errors.has('newPassword')}">
                <div v-show="errors.has('newPassword')"
                  class="invalid-feedback">{{ errors.first('newPassword') }}</div>
              </div>
              <div class="form-group">
                <label for="">確認密碼</label> <input v-model="confirmPassword"
                  type="password"
                  placeholder="確認密碼"
                  class="form-control"
                  v-validate="'required'"
                  name="confirmPassword"
                  :class="{'is-invalid':errors.has('confirmPassword')}">
                <div v-show="errors.has('confirmPassword')"
                  class="invalid-feedback">{{ errors.first('confirmPassword') }}</div>
              </div>
            </div>
          </div>
        </accordion> <hr>
        <accordion>
          <template slot="title"> 您的會員資格 : {{myPack.title}} </template>
          <template slot="btn"> 收費細節 </template>
          <div slot="body">
            <p v-html="myPack.desc"></p>
            <p> 下個付款日期：{{profile.payDate}} </p>
            <p> 月費在每個收費週期的開始日扣款，並可能在收費日期幾天才顯示於您的賬戶上。 </p>
            <table class="table mt-5">
              <thead>
                <th>日期</th>
                <th>付款內容 </th>
                <th>服務週期 </th>
                <th>付款方式 </th>
                <th>小計 </th>
                <th>合計 </th>
              </thead>
              <tbody>
                <tr v-for="(record,k) in profile.records"
                  :key="k">
                  <td>{{record.date}}</td>
                  <td>{{record.desc}}</td>
                  <td>{{record.cycle}}</td>
                  <td>{{record.payment}}</td>
                  <td>{{record.subtotal}}</td>
                  <td>{{record.total}}</td>
                </tr>
              </tbody>
            </table>
          </div>
        </accordion>
      </div>
    </div>
    <div class="block">
      <div class="block-header">會員層級 </div>
      <div class="block-body">
        <accordion>
          <template slot="title"> {{myPack.title}} :
            <span v-html="myPack.desc"></span>
          </template>
          <template slot="btn"> 更改會員層級 </template>
          <div slot="body">
            <div class="box"
              v-for="(pack,k) in packs"
              :key="k"
              :class="{'selected':profile.packId === pack.id}"
              @click="selectPack(pack.id)">
              <div class="hook">
                <i class="fa fa-check"></i>
              </div>
              <div class="pull-left">
                <h4>{{pack.title}}</h4>
                <span v-html="pack.desc"></span>
              </div>
              <div class="pull-right"> {{pack.priceInfo}} </div>
            </div>
          </div>
        </accordion>
      </div>
    </div>
    <div class="block">
      <div class="block-header">EMAIL 通知設定 </div>
      <div class="block-body">
        <div class="form">
          <div class="form-group">
            <div class="custom-control custom-checkbox"> <input type="checkbox"
                class="custom-control-input"
                id="customCheck1">
              <label class="custom-control-label"
                for="customCheck1">我願意接收EMAIL通知</label>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import accordion from '@/components/accordion';
import { mapGetters } from 'vuex'
export default {
  name: 'account-profile',
  computed: {
    ...mapGetters({
      // 取得資料夾列表
      packs: 'global/packs'

    }),
    myPack () {
      return this.packs.find(x => x.id === this.profile.packId)
    }
  },

  inject: {
    $validator: '$validator'
  },
  components: {
    accordion
  },

  data () {
    return {
      confirmPassword: '',
      profile: {
        nickname: '',
        phone: '',
        email: 'chieh@hmail.com',
        oldPassword: '',
        newPassword: '',
        records: [
          {
            date: '2018-09-04',
            desc: '無限收',
            cycle: '信用卡',
            payment: '信用卡',
            subtotal: 2000,
            total: 2000
          },
          {
            date: '2018-09-04',
            desc: '聽音樂',
            cycle: '信用卡',
            payment: '信用卡',
            subtotal: 2000,
            total: 2000
          },
          {
            date: '2018-09-04',
            desc: '無限 收聽音樂',
            cycle: '信用卡',
            payment: '信用卡',
            subtotal: 2000,
            total: 2000
          }
        ],
        packId: 3,
        payDate: '2018/3/3',

      }
    }
  },
  methods: {
    updateBasic () {

    },
    updatePassword () {

    },
    selectPack (id) {
      this.profile.packId = id
    }

  }
};
</script>
<style lang="scss" scoped>
@import "../../assets/scss/custom-bootstrap.scss";

.box {
  cursor: pointer;
  position: relative;
  padding: 1rem 3rem;
  width: 100%;
  display: block;

  float: left;
  margin-bottom: 2rem;
  background-color: #fff;
  border: 1px solid $primary;
  &:hover {
    background-color: #ebebeb;
  }
  .pull-right {
    font-size: 1.5rem;
  }
  .hook {
    display: none;
    background: $primary;
    position: absolute;
    border-radius: 100%;
    font-size: 2rem;
    width: 3rem;
    height: 3rem;
    color: #fff;
    text-align: center;
    line-height: 2.5rem;
    border: 3px solid #fff;
    left: -1.5rem;
    top: 50%;
    margin-top: -1.5rem;
  }
  &.selected {
    color: #fff;
    background-image: linear-gradient(to right, #f26c22 0%, #ffcf00 100%);
    background-repeat: repeat-x;
    .hook {
      display: block;
    }
  }
}
</style>



